const EnergyCard = () => (
  <TokenCard
    tokenAddress="0x024269E2057b904d1Fa6a7B52056A8580a85180F"
    tokenSymbol="BEG"
    tokenName="ENERGY"
    tokenDecimals={30}
    tokenImage={energyImage}
    tokenIcon={energyImage}
    // cardImage={energyCardImage}
    supply={500}
  />
);

const wide_layers = [
  {
    offset: 0,
    speed: 0,
    margin: 0,
    component: <HeaderCarousel />,
  },
  {
    offset: 2,
    speed: -0.1,
    margin: "30%",
    clickref: 0,
    centered: true,
    component: <MatterCard />,
  },
  {
    offset: 2,
    speed: -0.1,
    margin: "-30%",
    clickref: 0,
    centered: true,
    component: <AntimatterCard />,
  },
  {
    offset: 2,
    speed: -0.3,
    margin: 0,
    clickref: 0,
    centered: true,
    component: <EnergyCard />,
  },
];

const flat_layers = [
  {
    offset: 0,
    speed: 0,
    margin: 0,
    component: <HeaderCarousel />,
  },
  {
    offset: 2,
    speed: 0,
    clickref: 3,
    centered: true,
    component: <MatterCard />,
  },
  {
    offset: 3,
    clickref: 4,
    centered: true,
    component: <AntimatterCard />,
  },
  {
    offset: 4,
    margin: 0,
    clickref: 0,
    centered: true,
    component: <EnergyCard />,
  },
];

const wide_navigation = [
  {},
  {
    name: "ABOUT",
    href: 4,
  },
  {
    name: "BUY",
    href: 3,
  },
  {
    name: "TOKENS",
    href: 2,
  },
  // {
  //   name: "tokens",
  //   href: 0
  // },
  // {
  //   name: "tokens",
  //   href: 0
  // },
];

export default function Home() {
  const { widescreen } = useWindowDimensions();
  const navigation = widescreen ? wide_navigation : wide_navigation;
  const layers = widescreen ? wide_layers : flat_layers;
  const pages = widescreen ? 5 : 5;

  const parallax = useRef(null);
  const scrollTo = (page) => (e) => {
    e.preventDefault();
    parallax.current.scrollTo(page);
  };

  return (
    <div>
      <nav>
        <h4 onClick={scrollTo(0)}>METAVERSE (LOGO)</h4>
        {navigation.map((item, index) => {
          const { name, href } = item;
          const active = parallax.current?.offset === href;
          return (
            <a
              href="/#"
              className={active ? "active" : ""}
              key={index}
              onClick={scrollTo(href)}
            >
              {name}
            </a>
          );
        })}
      </nav>
      <Parallax ref={parallax} pages={pages}>
        {layers.map((layer, index) => {
          const { centered, clickref, component, margin, offset, speed } =
            layer;

          const centerStyles = centered
            ? {
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }
            : {};

          const marginStyles = margin
            ? {
                marginLeft: margin,
              }
            : {};

          const clickEvent =
            clickref !== undefined ? () => scrollTo(clickref) : () => {};

          return (
            <ParallaxLayer
              key={index}
              offset={offset}
              speed={speed}
              style={{ ...centerStyles, ...marginStyles }}
              onClick={clickEvent}
            >
              {component}
            </ParallaxLayer>
          );
        })}
      </Parallax>
    </div>
  );
}
